import { Message } from "element-ui";  
  
// MessageMessage  
const Kmessage = {  
    //   
    KmessageS: (resp) => {  
        return Message.success({  
            message: `${resp}`,  
            center: true // Message.success()  type   
        });  
    },  
    // KmessageE  
    KmessageE: (resp) => {  
        return Message.error({  
            message: `${resp}`,  
            center: true // Message.error()  type   
        });  
    }  
};  
  
// Kmessage  
export default Kmessage;